/* TabsComponent.css */
.tabs-container {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

    background-color: #1e1e1e; /* Dark background for a modern look */
    border-radius: 12px; /* More rounded corners for a softer look */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Stronger shadow for more depth */
    overflow: hidden; /* Ensures inner elements do not overflow */
  }
  
  .tabs-header {
    background-color: #444; /* Slightly lighter than the container for contrast */
    padding: 20px;
    border-bottom: 1px solid #333; /* Border to separate header from content */
  }
  
  .tabs-nav {
    display: flex;
    justify-content: space-around; /* Equal spacing between tab links */
    margin-bottom: -1px; /* To align the bottom border of active tab with content border */
  }
  
  .tab-link {
    background-color: transparent;
    border: none;
    padding: 15px 30px;
    font-size: 16px;
    color: #bbb; /* Light grey for inactive tabs */
    cursor: pointer;
    transition: color 0.3s, border-bottom 0.3s, transform 0.3s; /* Added transform for subtle hover effect */
    border-bottom: 3px solid transparent; /* For active tab border effect */
  }
  
  .tab-link.active {
    color: orangered; /* Bright color for active tab */
    border-bottom: 3px solid orangered; /* Match border color with text */
    background-color: #333;
  }
  
  .tab-link:hover {
    color: orangered; /* Match hover color with active state */
    transform: translateY(-2px); /* Slight lift on hover */
  }
  
  .tab-content {
    padding: 30px;
    background-color: #333; /* Dark background for content area */
    color: #fff; /* Light color for text */
    border-radius: 0 0 12px 12px; /* Rounded bottom corners to match container */
  }
  
  .tab-pane {
    display: none; /* Hide non-active tabs */
  }
  
  .tab-pane.active {
    display: block; /* Show active tab */
    background-color: #333;
  }
  
  /* Additional Styling for Better Visual Appeal */
  .tab-content h1, .tab-content h2, .tab-content h3, .tab-content h4, .tab-content h5, .tab-content h6 {
    color: orangered; /* Heading color matching active tab */
    margin-top: 0;
    margin-bottom: 20px;
  }
  
  .tab-content p {
    margin-bottom: 20px;
    line-height: 1.6; /* Better readability */
  }
  
  .tab-content a {
    color: white;
    text-decoration: none;
    transition: color 0.3s, text-shadow 0.3s;
  }
  
  .tab-content a:hover {
    color: black; /* Slightly darker hover color */
    text-shadow: 0 0 5px rgba(255, 111, 97, 0.5); /* Subtle glow on hover */
  }
  
  .tab-content ul, .tab-content ol {
    padding-left: 20px;
    margin-bottom: 20px;
  }
  
  .tab-content ul li, .tab-content ol li {
    margin-bottom: 10px;
  }
  
  .tab-content blockquote {
    margin: 20px 0;
    padding: 20px;
    background: #444; /* Slightly lighter background for blockquotes */
    border-left: 5px solid orangered; /* Accent color for blockquote border */
    font-style: italic;
    color: #ccc; /* Lighter text color */
    border-radius: 5px;
  }
  
  .tab-content code {
    background-color: #2e2e2e; /* Dark background for code blocks */
    color: orangered; /* Accent color for code text */
    padding: 2px 5px;
    border-radius: 4px;
  }
  
  .tab-content pre {
    background-color: #2e2e2e; /* Dark background for preformatted text */
    color: #fff;
    padding: 15px;
    border-radius: 8px;
    overflow-x: auto;
  }
  
  /* Responsive Enhancements */
  @media (max-width: 768px) {
    .tab-link {
      padding: 10px 20px;
      font-size: 14px;
    }
  
    .tab-content {
      padding: 20px;
    }
  }