.tab-pane {
    padding: 20px;
    text-align: center; /* Center the text inside the tab pane */
  }
  
  .support-header {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .support-intro {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .support-links {
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    gap: 20px;
  }
  
  .support-item {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center the content inside each item */
    flex: 1;
  }
  
  .support-link {
    background-color: black;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    margin-bottom: 5px;
    text-align: center;
  }
  
  .support-link:hover {
    background-color: orangered;
  }
  
  .description {
    font-size: 14px;
    color: #666;
    margin: 0;
    text-align: center; /* Center the description text */
  }
  