/* src/css/Home.css */
.home-container {
    max-width: 800px;
    margin: 40px auto;
    padding: 30px;
    background-color: #2e2e2e; /* Darker background for better contrast */
    color: #eee; /* Lighter text color for readability */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  .home-title {
    font-size: 2rem;
    color: orangered; /* Accent color for the title */
    margin-bottom: 20px;
    text-align: center;
  }
  
  .home-description {
    font-size: 1.2rem;
    line-height: 1.6;
  }
  
  .home-description strong {
    color: orangered; /* Accent color for highlighted text */
  }
  