/* Taigrbot.css */
.taigrbot {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff; /* White background */
    color: #333; /* Black text color */
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Shadow for container */
  }
  
  .header {
    text-align: center;
    color: orangered;
  }
  
  .custom-icon {
    width: 60px;
    height: 60px;
    margin: 0 auto 10px;
    border-radius: 50%;
    background-size: cover;
  }
  

  /* Styling for code elements */
code {
  background-color: #2d2d2d;
  color: #f8f8f2;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.1em;
  margin: 0 2px;
}

.taigrboth1 {
  color: orangered;
  text-align: center;
}