.cv-maker-container {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    font-family: Arial, sans-serif;
    gap: 20px; /* Space between form and preview */
  }
  
  .form-container {
    width: 100%;
    max-width: 400px; /* Restrict width */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .form-container input,
  .form-container textarea {
    width: calc(100% - 20px); /* Adjust width */
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .form-container input[type="file"] {
    border: none;
    padding: 0;
  }
  
  .form-container button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 10px;
  }
  
  .form-container button:hover {
    background-color: #0056b3;
  }
  
  .delete-btn {
    background-color: #dc3545;
    color: #fff;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-left: 10px;
  }
  
  .delete-btn:hover {
    background-color: #c82333;
  }
  
  .cv-preview {
    flex: 1; /* Take remaining space */
    max-width: 600px; /* Restrict width */
    padding: 20px;
    border: 1px solid #ddd;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    position: relative; /* Ensure elements are properly positioned */
  }
  
  .cv-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .cv-picture {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .cv-header h1 {
    margin: 0;
    font-size: 28px;
    color: #333;
  }
  
  .cv-header p {
    margin: 5px 0 0;
    font-size: 18px;
    color: #666;
  }
  
  .cv-section {
    margin-bottom: 20px;
  }
  
  .cv-section h2 {
    border-bottom: 2px solid #333;
    padding-bottom: 5px;
    margin-bottom: 10px;
    font-size: 22px;
    color: #333;
  }
  
  .cv-section ul {
    list-style: none;
    padding: 0;
  }
  
  .cv-section ul li {
    margin-bottom: 5px;
    font-size: 16px;
    color: #666;
  }
  
  .cv-section p {
    font-size: 16px;
    color: #666;
  }
  
  .cv-buttons {
    text-align: center;
    margin-top: 20px;
  }
  
  .cv-buttons button {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 0 10px;
  }
  
  .cv-buttons button:hover {
    background-color: #0056b3;
  }
  